body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: #f0f2f5;
}

.form-container {
  max-width: 400px;
  margin: 50px auto;
  background: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 0 10px #ccc;
}

.form-container input {
  display: block;
  margin: 15px auto;
  padding: 12px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-container button {
  padding: 12px 20px;
  background: #128c7e;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.chat-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 280px;
  min-width: 200px;
  border-right: 1px solid #ddd;
  padding: 15px;
  background: white;
  overflow-y: auto;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-friend-btn {
  background: #128c7e;
  border: none;
  color: white;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.friend-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px;
  cursor: pointer;
  border-bottom: 1px solid #eee;
  transition: background 0.2s;
}

.friend-item:hover {
  background: #f0f0f0;
}

.friend-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.chat-window {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #e5ddd5;
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background: #075e54;
  color: white;
}

.chat-header .chat-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.friend-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 8px;
}

.user-profile img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.sent {
  text-align: right;
  margin: 10px 0;
}

.sent span {
  display: inline-block;
  background: #dcf8c6;
  padding: 8px 12px;
  border-radius: 10px;
  max-width: 70%;
  word-break: break-word;
}
.received span{
  display: inline-block;
  background-color: whitesmoke;
  padding: 8px 12px;
  border-radius: 10px;
  max-width: 70%;
  word-break: break-word;
}

.received {
  text-align: left;
  margin: 10px 0;
}

.input-area {
  display: flex;
  padding: 15px;
  background: #f0f0f0;
}

.input-area input {
  flex: 1;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin-right: 10px;
}

.input-area button {
  padding: 12px 20px;
  background: #128c7e;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.back-btn {
  background: #075e54;
  color: white;
  border: none;
  padding: 10px 15px;
  margin: 10px;
  cursor: pointer;
  border-radius: 4px;
}

@media (max-width: 768px) {
  .chat-container {
    flex-direction: column;
  }

  .chat-container .sidebar {
    display: block;
  }

  .chat-container.mobile-chat-open .sidebar {
    display: none;
  }

  .chat-container .chat-window {
    display: none;
  }

  .chat-container.mobile-chat-open .chat-window {
    display: flex;
    flex-direction: column;
    flex: 1;
  }
}
.friend-item {
  position: relative;
  display: flex;
  align-items: center;
  margin: 8px 0;
  cursor: pointer;
}

.avatar-wrapper {
  position: relative;
}

.friend-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.online-dot {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #00ff00;
  border: 2px solid white;
  border-radius: 50%;
  width: 10px;
  height: 10px;
}
.sent,
.received {
  position: relative;
  max-width: 60%;
  padding: 8px 35px 8px 10px; /* right padding for tick */
  margin: 5px 0;
  border-radius: 8px;
}

.sent {
  /* background: #dcf8c6; */
  margin-left: auto;
}

.received {
  /* background: whitesmoke; */
}
.tick {
  position: absolute;
  bottom: 5px;
  right: 8px;
  font-size: 16px;
  display: flex;
  align-items: center;
}
.dropzone {
  border: 2px dashed #aaa;
  padding: 20px;
  text-align: center;
  margin: 10px;
  cursor: pointer;
  border-radius: 8px;
}
.file-preview img {
  max-width: 200px;
  max-height: 200px;
  border-radius: 8px;
}

.messages img {
  max-width: 200px;
  max-height: 200px;
  border-radius: 8px;
  margin-top: 5px;
}

.selected {
  background: #c2bbbbc9;
  border: 1px solid #aaa;
}
/* ✅ Modal overlay */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(5px);
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* ✅ Modal box with glass effect */
.modal-content {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 16px;
  padding: 30px 25px;
  width: 320px;
  text-align: center;
  color: #fff;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
}

/* ✅ Modal title */
.modal-content h4 {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
}

/* ✅ Dropdown */
.modal-content select {
  width: 100%;
  padding: 10px 12px;
  margin-bottom: 20px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  backdrop-filter: blur(8px);
  appearance: none;
}

/* ✅ Buttons container */
.modal-content .modal-buttons {
  display: flex;
  justify-content: space-between;
}

/* ✅ Buttons */
.modal-content button {
  flex: 1;
  padding: 10px 15px;
  margin: 0 5px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s ease;
  font-weight: 500;
}

/* ✅ Confirm button */
.modal-content button:first-of-type {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: #fff;
}

.modal-content button:first-of-type:hover {
  opacity: 0.9;
}

/* ✅ Cancel button */
.modal-content button:last-of-type {
  background: linear-gradient(135deg, #ff416c 0%, #ff4b2b 100%);
  color: #fff;
}

.modal-content button:last-of-type:hover {
  opacity: 0.9;
}
